.buttons-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /* 3 columns with equal width */
  grid-gap: 10px;
  /* Adjust the gap as needed */
  justify-items: center;
  /* Center buttons horizontally */
  align-items: center;
  /* Center buttons vertically */
  height: 100%
}

.buttons-container button {
  /* Remove the default button background and border */
    background: none;
    border: none;
  
    /* Add a black outline (border) with a 2px width */
    border: 2px solid black;
  
    /* Apply custom font 'Squada One' */
    font-family: 'Squada One', sans-serif;

    /* Increase the font size */
    font-size: 24px;
    /* Adjust the size as needed */
  
    /* Set the text color to black */
    color: white;
    -webkit-text-stroke: 1px black;

    /* Add padding and margin for spacing */
    padding: 10px 20px;
    margin: 5px;
  
    /* Set cursor to pointer on hover for better UX */
    cursor: pointer;
  
    /* Optional: Add transition for a smoother hover effect */
    transition: border-color 0.3s;
}
