.buttons-container button::after {
  content: attr(data-cost);
  /* Use the data-label attribute for the small text */
  display: block;
  font-size: 18px;
  /* Adjust the size as needed */
  margin-top: 5px;
  /* Add spacing between button and small text */
  color: white;
  /* Text color for the small text */
}